<template>
  <div ref="scroller" class="messages-list box-card">
    <div v-for="message in messages" :key="message.id">
      <span><em class="text-primary">{{ message.user_email }}:</em> {{ message.text }}</span>
      <br/>
      <span style="opacity: 0.7; font-size: 12px;" class="text-secondary">{{ message.created_at | formatDate }}</span>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'app-chat-messages-list',

    updated() {
      const el = this.$refs.scroller
      // fix scroll bar to the bottom 
      el.scrollTop = 2000
      // console.log(el.scrollLeft, el.scrollTop);
    },

    computed: {
      messages() {
        return this.$store.getters['message/list']
      }
    }
  }
</script>

<style scoped>
  .messages-list {
    max-height: 500px;
    overflow-y: scroll;
    margin-bottom: 20px;
  }
</style>


