import axios from 'axios';


export default function makeAuthActions (feathers, store) {
  const message = feathers.service('messages')
  const user = feathers.service('users')

  message.on('created', (data) => {
    console.log('onCreate ', data)
    store.dispatch('message/onCreate', data)
  })


  const actions = {
    onCreate({ commit }, data) {
      commit('newMessage', data)
      return data
    },
    async create ({ commit }, payload) {
      console.log('cc ', payload)
      const res = await message.create(payload)
      console.log(res)
      return res
    },

    async list({ commit }, payload) {
      const res = await message.find()
      commit('setMessages', res)
      return res
    }
   
  }


  return actions
}


